<template>
  <div class="view-games">    
    <!-- Upper Section -->
    <div class="upper">
      <h1>Games Dashboard</h1>
      <button type="button" class="btn" @click="showModal">Create</button>
    </div>
    <!-- Lower Section -->
    <div class="lower">
      <Games :addedGame="addedGame">
      </Games>      
    </div>
    <div class="create-modal" v-show="isModalVisible">
      <!-- Modal allows us to use the Modal component popup. -->
      <Modal @close="closeModal">
            <!--The templates below replace the original Modal content with items below -->
            <template v-slot:header>
                Create a Game
            </template>
            <template v-slot:body>
                <!--Form for create a game. Need to create a game object and send to backend database-->
                <form @submit.prevent="createGame" class="form">
                  <div>
                    <label for="game-name">Game Name</label>
                    <input type="text" id="game-name" v-model="form.game_name" required/>
                  </div>
                  <div>
                    <label for="start-date">Start Date</label>
                    <input type="date" id="start-date" v-model="form.start_date"  required/>
                  </div>
                  <div>
                    <label for="end-date">End Date</label>
                    <input type="date" id="end-date" v-model="form.end_date" required/>
                  </div>
                  <button type="submit" class="btn">Create</button>
                </form> 
            </template>
            <template v-slot:footer><p></p></template>
       </Modal>
      </div>
    </div>
    
  </template>
  
  
<script>
import Modal from '../components/Modal.vue';
import Games from '../components/GamesList.vue';
import gameService from '../services/GameService';

  export default {
    components: {
        Modal,
        Games
    },
    data(){
        return {
          form: {
            game_name:"",
            start_date:"",
            end_date:"",
          },
          addedGame: {},
          isModalVisible: false,
        }
    },
    methods: {
        showModal(){
          if (this.$store.state.token === '') {
            window.alert("You need to login to be able to create a game.");
            return;
          }
          this.isModalVisible=true;
        },
        closeModal(){
          this.isModalVisible=false;
        },
        createGame() {        
          gameService.createGame(this.form).then((response) => {
            if (response.status === 200) {
              (response.status);
              this.isModalVisible = false;
              this.form = {game_name:"", start_date:"", end_date:""}
              this.addedGame = response.data;
              window.alert("Game created successfully");
            }
          })
          .catch((error) => {
            (error);
            window.alert("Game already exists");
          });
        }
    }

  };
  </script>
  
  <style scoped>
.view-games {
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  border-left: 1px solid rgb(167, 164, 164);
  border-right: 1px solid rgb(167, 164, 164);
  min-height: calc(100vh - 47.5px);
}


.upper {
  background: radial-gradient(at top left, #4a6384, #778DA9);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 22px;
  height: 100px;
  width: 100%;
  padding: 30px 0;
}
.upper > h1 {
  margin: 0;
}
.upper > .btn {
  color: white;
  background-color: rgb(27, 38, 59);
  padding: 5px;
  border-radius: 5px;
}
.lower {
  width: 1400px;
  flex-grow: 1;
  border-left: 1px solid rgb(173, 173, 173);
  border-right: 1px solid rgb(173, 173, 173);
}
.create-modal {
  top: 0;
  position: fixed;
  background-color: rgb(0, 0, 0, 0.5);
  width: 100vw;
  height: 100vh;
}
form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 20px;
}
form > div {
  display: flex;
  gap: 5px;
}
</style>





