<template>
    <div class="stock">
        <h3>{{stock.name}}</h3>
        <p>{{ stock.T }}</p>
        <div>
            <h4>{{ stock.current_price.toFixed(2) }}</h4>
            <p>USD</p>
            <p :class="{increase: stock.change >= 0, decrease: stock.change < 0}">{{stock.change >= 0 ? "+" : ""}}{{ stock.change.toFixed(2) }}%</p>  
        </div>
        <p>Quantity owned: {{ quantityOwned > 0 ? quantityOwned.toFixed(2) : 0 }}</p>
        <p>Value owned: ${{ quantityOwned > 0 ? (quantityOwned * stock.current_price).toFixed(2) : 0}}</p>
        <div>
            <label for="stockQuantity">Quantity to buy/sell:</label>
            <input type="number" v-model="quantity" @input="changeValue">
        </div>
        <div>
            <label for="stockValue">Dollar value to buy/sell:</label>
            <input type="number" v-model="value" @input="changeQuantity">
        </div>   
        <div class="buttons">
            <button @click="buyStock">Buy Stock</button>
            <button @click="sellStock(false)">Sell Stock</button>
            <button @click="buyAll">Buy All</button>
            <button @click="sellAll">Sell All</button>
        </div>
        <br>
        <div>
            <p>Get data for a specific date:</p>
            <input type="date" v-model="date" @change="selectDate">
        </div>
        <div class="date-info" v-if="date != ''">
            <svg height="200" width="100%" class="svg-graph">
                <rect width="100%" height="100%" class="keep" fill="white"/>
            </svg>
            <div>
                <p>Open: {{ infoForDate.open.toFixed(2) }}</p>
                <p>Lowest: {{ infoForDate.min.toFixed(2) }}</p>
            </div>
            <div>
                <p>Close: {{ infoForDate.close.toFixed(2) }}</p>      
                <p>Highest: {{ infoForDate.max.toFixed(2) }}</p>
            </div>
            <div>
                <p :class="{increase: infoForDate.change >= 0, decrease: infoForDate.change < 0}">{{infoForDate.change >= 0 ? "+" : ""}}{{ infoForDate.change.toFixed(2) }}%</p>   
                <p>Trading volume: {{ infoForDate.volume }}</p>
            </div>
            
        </div>
    </div>
</template>
<script>
import TradeService from "../services/TradeService.js";
import PortfolioService from '../services/PortfolioService';
import GameService from '../services/GameService';
import stockService from '../services/StockService';

export default {
    props: {
        stock: Object,
        gameId: Number
    },
    data() {
        return {
            balance: 0,
            quantityOwned: 0,
            quantity: 0,
            value: 0,
            date: "",
            infoForDate: {min: 0, max: 0, open: 0, close: 0, volume: 0, change: 0},
        }
    },
    methods: {
        selectDate(event) {
            const yesterday = new Date();
            yesterday.setDate(yesterday.getDate() - 1);
            if (new Date(event.target.value) > yesterday) {
                window.alert("Please select a date prior to today.")
                this.date = "";
                return;
            }
            stockService.getStockByDate(this.stock.T, this.date).then((response) => {
                if (response.status === 200 || response.status === 201) {
                    const graph = response.data;
                    this.infoForDate.min = graph.min;
                    this.infoForDate.max = graph.max;
                    this.infoForDate.open = graph.open;
                    this.infoForDate.close = graph.close;
                    this.infoForDate.volume = graph.volume;
                    this.infoForDate.change = (graph.close - graph.open) * 100 / graph.open;
                    const points = [];
                    var svg = document.querySelector(".svg-graph");
                    var linesToRemove = svg.querySelectorAll(":not(.keep)");
                    linesToRemove.forEach(line => {
                        svg.removeChild(line);
                    });
                    const width = svg.getBoundingClientRect().width;
                    for (var i = 0; i < response.data.graph.length; i++) {
                        points[i] = {x: (graph.distance * i * width).toFixed(2), y: (200 - graph.graph[i] * 200).toFixed(2)}        
                    }            
                    for (var j = 0; j < points.length - 1; j++) {      
                        var line = document.createElementNS("http://www.w3.org/2000/svg", "line");
                        line.setAttribute("x1", points[j].x);
                        line.setAttribute("y1", points[j].y);
                        line.setAttribute("x2", points[j + 1].x);
                        line.setAttribute("y2", points[j + 1].y);
                        line.setAttribute("stroke", "rgb(65, 199, 65)");
                        line.setAttribute("stroke-width", "2");
                        svg.appendChild(line);
                    }
                }
            }).catch(() => {
                window.alert("There is no stock data for the selected date/Too many requests.");
                this.date = "";
            })
        },
        changeQuantity(event) {
            this.quantity = event.target.value / this.stock.current_price
        },
        changeValue(event) {
            this.value = event.target.value * this.stock.current_price
        },
        buyStock() {
            if (this.value > this.balance) {
                window.alert("Please enter an amount equal to or less than your current balance.")
                return;
            }
            const trade = {
                stock_ticker: this.stock.T,
                trade_quantity: this.quantity,
                stock_trade_price: this.stock.current_price,
                trade_date: new Date().getTime()
            }
            TradeService.buyStock(this.gameId, trade).then(response => {
                if (response.status === 200 || response.status === 201) {
                    window.alert("You have successfully bought the stock.");
                    this.$emit('update');
                }
            });
            this.quantityOwned += parseFloat(this.quantity);
            this.balance -= parseFloat(this.value);
            this.quantity = 0;
            this.value = 0;
        },
        buyAll() {
            if (this.balance == 0) {
                window.alert("You don't have any money.");
                return;
            }
            this.quantity = this.balance / this.stock.current_price;
            this.value = this.balance;
            this.buyStock();
            
        },        
        sellStock(all) {
            if (this.quantity > this.quantityOwned) {
                window.alert("Please enter an amount equal to or less than your current holdings.")
                return;
            }
            const trade = {
                stock_ticker: this.stock.T,
                trade_quantity: this.quantity,
                stock_trade_price: this.stock.current_price,
                trade_date: new Date().getTime()
            }
            TradeService.sellStock(this.gameId, trade, all).then(response => {
                if (response.status === 200 || response.status === 201) {
                    window.alert("You have successfully sold the stock.");
                    this.$emit('update')
                }
            })
            this.quantityOwned -= parseFloat(this.quantity);
            this.balance += parseFloat(this.value);
            this.quantity = 0;
            this.value = 0;
        },
        sellAll() {
            if (this.quantityOwned == 0) {
                window.alert("You don't own this stock.");
                return;
            }
            this.quantity = this.quantityOwned;
            this.value = this.quantity * this.stock.current_price;
            this.sellStock(true);
        },
        getQuantityOwned(stock) {
            PortfolioService.getQuantityOwned(stock.T, this.gameId).then(response => {
                if (response.status == 200) {
                    this.quantityOwned = response.data;
                }
            }).catch(() => {
                this.quantityOwned = 0;
            })  
        }
    },
    created() {
        this.getQuantityOwned(this.stock);
        GameService.getUserBalance(this.gameId).then(response => {
            this.balance = response.data;
        })
    },
    watch: {
        stock(stock) {
            this.getQuantityOwned(stock);
            this.date = "";
            this.quantity = 0;
            this.value = 0;
        },
    }
};
</script>
    
<style scoped>
.stock {
    color: white;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    padding: 20px 20px 0px 20px;
}
.stock > div {
    display: flex;
    gap: 5px;
}
.stock > div > label {
    width: 170px;
}
.stock > h3, .stock > p, .stock > div > h4, .stock > div > p {
    margin: 0;
}
.stock > div > .increase, .stock > div > .decrease {
    margin-left: 18px;
}
.stock > div > .increase, .date-info > div > .increase {
    color: rgb(65, 199, 65); 
}
.stock > div > .decrease, .date-info > div > .decrease {
    color: red;
}
.stock > .buttons > button {
    width: 80px;
}
.stock > .date-info {
    flex-direction: column;
    width: 100%;
}
.stock > .date-info > div {
    display: flex;
}
.stock > .date-info > div > p {
    margin: 0;
}
.stock > .date-info > div > p:first-child {
    width: 40%;
}
/*This disables the up and down arrows in the number input field */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

</style>
