import axios from 'axios';

export default {
    getAllGames() {
        return axios.get('/games')
    },
    getGameById(gameId) {
        return axios.get(`/games/${gameId}`)
    },
    getGamesForUser() {
        return axios.get(`/games/participating`)
    },
    getUsersForGame(gameId) {
        return axios.get(`/games/${gameId}/users`)
    },
    getLeaderboard(gameId) {
        return axios.get(`/games/${gameId}/leaderboard`)
    },
    getUserBalance(gameId) {
        return axios.get(`/games/${gameId}/balance`)
    },
    createGame(gameObject) {
        return axios.post('/games/create', gameObject)
    },
    joinGame(gameId) {
        return axios.post(`/games/${gameId}/join`)
    },
    leaveGame(gameId) {
        return axios.delete(`/games/${gameId}/leave`)
    },
    setWinner(gameId) {
        return axios.post(`/games/${gameId}/winner`)
    },
    getGlobalLeaderboard() {
        return axios.get('/leaderboard')
    }
}