<template>
    <div class="all-stocks" >
        <div class="search-bar">
            <input @keyup.enter="searchStocks" type="text" placeholder="Search By Ticker" v-model="searchTerm">
            <button @click="searchStocks">Search</button>
            <div class="nothing"></div>
            <select @change="sortStocks" class="sort-stocks">
                <option value="">Sort By</option>
                <option value="price-down">Descending Price</option>
                <option value="price-up">Ascending Price</option>
                <option value="increase">Highest Increase</option>
            </select>
        </div>
        <div class="table-container">
            <table>
                <tr>
                    <th class="ticker-column">Ticker</th>
                    <th class="name-column">Company Name</th>
                    <th class="number-column">Price</th>
                    <th class="number-column">Change</th>
                </tr>
                <tr v-for="stock in pagedStocks" :key="stock.stock_ticker" @click="openBuyMenu(stock)">
                    <td class="ticker-column">{{ stock.T }} </td>
                    <td class="name-column">{{ stock.name}}</td>
                    <td class="number-column">{{ stock.current_price.toFixed(2) }} </td>
                    <td class="number-column" :class="{increase: stock.change >= 0, decrease: stock.change < 0}">{{stock.change >= 0 ? "+" : ""}}{{ stock.change.toFixed(2) }}%</td>
                </tr>
            </table>
        </div> 
        <div class="pagers" v-if="this.stocks.length > 50">
            <div v-for="page in displayedPages" :key="page" @click="changePage(page)" :class="{selected: pager + 1 === page}"><p>{{page}}</p></div>         
        </div>
    </div>
</template>

<script>
import stockService from '../services/StockService';

export default {
    props: {
        game: Object
    },
    data() {
        return {
            allStocks: [],
            stocks: [],     
            pager: 0,
            pages: [],
            displayedPages: [],
            searchTerm: "",
        }
    },
    methods: {
        openBuyMenu(stock) {
            this.$emit('openBuyMenu', stock);
        },
        stockMenu(){
            stockService.getAllStocks().then((response) => {
                if(response.status === 200) {
                    this.stocks = response.data;
                    this.pages = Math.ceil(this.stocks.length / 50)
                    if (this.pages > 9) {
                        this.displayedPages = Array.from({ length: 8 }, (_, index) => index + 1);
                        this.displayedPages.push("...")
                        this.displayedPages.push(this.pages)
                    } else {
                        this.displayedPages = Array.from({length: this.pages}, (_, index) => index + 1);
                    }
                    this.stocks.forEach(stock => {
                        stock.change = (stock.c - stock.o) * 100 / stock.o
                    })
                    this.allStocks = this.stocks;
                }
            });
        },
        sortStocks(event) {
            if (event.target.value === "price-down") {
                this.stocks = this.stocks.sort((a, b) => b.c - a.c)
            } else if (event.target.value === "price-up") {
                this.stocks = this.stocks.sort((a, b) => a.c - b.c)
            } else if (event.target.value === "increase") {
                this.stocks = this.stocks.sort((a, b) => b.change - a.change)
            }
        },
        searchStocks() {
            this.stocks = this.allStocks.filter(s => s.T.toLowerCase().includes(this.searchTerm.toLowerCase()));     
            document.querySelector(".sort-stocks").value = "";
            this.pages = Math.ceil(this.stocks.length / 50)
            if (this.pages > 9) {
                this.displayedPages = Array.from({ length: 8 }, (_, index) => index + 1);
                this.displayedPages.push("...")
                this.displayedPages.push(this.pages)
            } else {
                this.displayedPages = Array.from({length: this.pages}, (_, index) => index + 1);
            }
            this.pager = 0;
        },
        changePage(page) {
            if (page !== "...") {
                this.pager = page - 1
                if (this.displayedPages.length !== this.pages) {
                    if (page > 5 && page < this.pages - 4) {
                        this.displayedPages = Array.from({ length: 7 }, (_, index) => index + page - 3);
                        this.displayedPages.push("...", this.pages);
                        this.displayedPages.unshift(1, "...")
                    } else if (page >= this.pages - 4) {
                        this.displayedPages = Array.from({length: 8}, (_, index) => index + this.pages - 7);
                        this.displayedPages.unshift(1, "...")
                    } else {
                        this.displayedPages = Array.from({ length: 8 }, (_, index) => index + 1);
                        this.displayedPages.push("...", this.pages);
                    }
                }
            } 
        }
    },
    computed: {
        pagedStocks() {
            return this.stocks.slice(this.pager * 50, this.pager * 50 + 50)
        }
    },
    created() {
        this.stockMenu();
    }

}
</script>

<style scoped>
.all-stocks {
    background: rgb(151, 167, 194);
    width: 67%;
    display: flex;
    flex-direction: column;
}
.search-bar {
    display: flex;
    gap: 10px;
    padding: 5px;
}
.nothing {
    flex-grow: 1;
}
.table-container {
    overflow-y: auto;
}
.table-container > table {
    border-collapse: collapse;
    width: 100%;
    table-layout: fixed;
}
.table-container > table > tr > th, .table-container > table > tr > td {
  min-width: 80px;
  padding: 12px 10px;
  text-align: left;
}
.table-container > table > tr > .ticker-column {
    width: 10%;
}
.table-container > table > tr > .name-column {
    width: 70%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.table-container > table > tr > .number-column {
    width: 10%;
}
.table-container > table > tr:has(td) {
    cursor: pointer
}
.table-container > table > tr:nth-child(odd) {
  background-color: #778DA9;
}
.table-container > table > tr:nth-child(even) {
  background-color: #8d9eb5;
}
.table-container > table > tr:has(td):hover {
  background-color: rgb(90, 101, 130);
}
.pagers {
    padding: 5px 0px;
    display: flex;
    gap: 20px;
    justify-content: center;
}
.pagers > div {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    background-color: rgb(180, 180, 180);
    font-size: 13px;
}
.pagers > .selected {
    background-color: #778DA9;
}
.decrease {
    color: red;
}
.increase {
    color: rgb(32, 113, 32);
}
</style>