<template>

    <ViewGamesModal class="games">
        <template v-slot:header>
            <div class="search-bar">
                <input type="text" v-model="searchTerm" placeholder="Search by Name">
                <div>
                    <button @click="viewAll">View All</button>
                    <button @click="viewActiveGames" :class="{selected: status === 'active'}">Active Games</button>
                    <button @click="viewCreatedGames" :class="{selected: status === 'created games'}">Created Games</button>     
                    <button @click="viewJoinedGames" :class="{selected: status === 'joined games'}">Joined Games</button>
                </div>  
            </div>
                  
        </template>
        <template v-slot:body>
            <div class="cards">
                <div v-for="game in filteredGames"  :key="game.game_id" @click="openGame(game.game_id)">
                    <GameCard :game="game"/>
                </div>     
            </div>       
        </template>
        <template v-slot:footer><p></p></template>
    </ViewGamesModal>      
</template>
    
    
<script>
    import ViewGamesModal from './ViewGamesModal.vue';
    import gameService from '../services/GameService';
    import GameCard from '../components/GameCard.vue';
    export default {
        components: {
            GameCard,
            ViewGamesModal
        },
        props: {
            addedGame: Object
        },
        data(){
            return {
                userGames: [],
                games: [],
                searchTerm: "",
                status: "",
            }
        },
        created() {
            gameService.getAllGames().then(response => {
                this.games = response.data;
            })
            gameService.getGamesForUser().then(response => {
                this.userGames = response.data
                this.$store.commit('SET_GAMES', this.userGames)
            })  
        },
        methods: {
            viewCreatedGames() {
                this.status = "created games"
            },
            viewJoinedGames() {
                this.status = "joined games"
            },
            viewAll() {
                this.status = ""
            },
            viewActiveGames() {
                this.status = "active"
            },
            openGame(id) {
                this.$router.push({name: 'game', params: {game_id: id}});
            },
            close() {
                this.$emit('close')
            }
        },
        computed: {
            filteredGames() {
                if (this.status === "joined games") {
                    return this.games.filter(game => this.userGames.includes(game.game_id) && game.game_name.toLowerCase().includes(this.searchTerm))
                } else if (this.status === "created games") {
                    return this.games.filter(game => game.creator_id === this.$store.state.user.id && game.game_name.toLowerCase().includes(this.searchTerm))
                } else if (this.status === "active") {
                    return this.games.filter(game => game.winner === null && game.game_name.toLowerCase().includes(this.searchTerm))
                } else {
                    return this.games.filter(game => game.game_name.toLowerCase().includes(this.searchTerm))
                }
                
            }
        },
        watch: {
            addedGame(game) {          
                this.games.push(game);
            }
        }
    };
</script>
    
<style scoped>
.games {
    padding: 0px 50px 20px 50px;
    width: calc(100% - 100px);
}
.search-bar {
    display: flex;
    align-items: flex-start;
    gap: 20px;
}
.search-bar > div {
    display: flex;
    gap: 10px;
}
.search-bar > div > .selected {
    box-shadow: 0 0 5px darkblue;
}
.cards {
    display: flex;
    justify-content: flex-start;
    gap: 20px;
    flex-wrap: wrap;
}
.cards > div {
    width: 300px;
}
</style>
