<template>
  <div id="app">
    <div class="nav" v-if="$route.name != 'login' && $route.name != 'register'">
      <p v-if="$store.state.token != ''">Welcome, {{ $store.state.user.username }}</p>
      <div class="empty-space"></div>
      <p class="button-link" @click="resetTickers" v-if="isMod">Reset Tickers</p>
      <p class="button-link" @click="resetDatabase" v-if="isMod">Reset Database</p>
      <router-link class="button-link" v-bind:to="{ name: 'view-games' }">Games Dashboard</router-link>
      <router-link class="button-link" v-bind:to="{ name: 'global-leaderboard' }">Leaderboard</router-link>
      <router-link class="button-link" v-bind:to="{ name: 'login'}" v-if="$store.state.token === ''">Login</router-link>
      <router-link class="button-link" v-bind:to="{ name: 'logout' }" v-if="$store.state.token != ''">Logout</router-link>
    </div>
    <router-view />
  </div>
</template>

<script>
import stockService from './services/StockService';
import TickerService from './services/TickerService.js';
export default {

    methods: {
      resetDatabase() {
        const response = window.confirm("Do you want to reset the database?");
        if (response) {
          stockService.resetDatabase().then(response => {
            if (response.status === 200 || response.status === 201) {
              window.alert("Database has been reset.")
            }
          })
        }
        
      },
      resetTickers() {
        const response = window.confirm("Do you want to reset the tickers?");
        if (response) {
          TickerService.resetTickers().then(response => {
            if (response.status === 200 || response.status === 201) {
              window.alert("Tickers have been reset.")
            }
          })
        }
      }
    },
    computed: {
      isMod() {
        return ["quang", "nick", "james"].includes(this.$store.state.user.username);
      }
    }
};

</script>

<style>
#app {
  font-family: Arial;
  min-height: 900px;
}
.nav {
  overflow: hidden;
  background-color: white;
  position: inherit;
  display: flex;
  align-items: center;
  height: 47.5px;
}
.nav > p {
  padding-left: 20px;
}
.nav > .empty-space {
  flex-grow: 1;
}

/* Links inside the navbar */
.nav .button-link {
  float: left;
  font-size: 16px;
  color:  black;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  cursor: pointer;
}


/* Add a red background color to navbar links on hover */
.nav .button-link:hover, .dropdown:hover .dropbtn {
  background-color: #E0E1DD;
}

/* Dropdown content (hidden by default) */
</style>