<template>
    <div class="game">
        <div class="title-background">
            <div class="info">
                <h1>{{ this.game.game_name }}</h1>
                <p>Start Date: {{ new Date(game.start_date).toLocaleDateString() }} </p>
                <p>End Date: {{ new Date(game.end_date).toLocaleDateString() }}</p>
                <p>Winner: {{ game.winner != null ? game.winner : "N/A"}} </p>
            </div>
            <button type="button" class="btn" @click="showModal" v-if="isJoinVisible && !isCreator && game.winner === null">Join</button>
            <button type="button" class="btn" @click="endGame" v-if="isCreator && game.winner === null">End Game</button> 
        </div>
        <div class="join-box" v-if="isModalVisible">
            <Modal @close="closeModal">
                <template v-slot:header>
                    Would you like to join this game?
                </template>
                <template v-slot:body>
                    <form @submit.prevent="joinGame" class="form">
                        <button type="submit" class="btn">Yes</button>
                    </form>
                </template>
                <template v-slot:footer>
                    <p></p>
                </template>
            </Modal>
        </div>
        <div class="interface">
            <StockList @openBuyMenu="openBuyMenu"/>
            <div class="pf-lb">
                <div class="buttons">            
                    <button type="button" class="btn" :class="{'active': display === 'Leaderboard'}" @click="display = 'Leaderboard'">Leaderboard</button>
                    <button type="button" class="btn" :class="{'active': display === 'Portfolio'}" @click="display = 'Portfolio'">Portfolio</button>
                    <button type="button" class="btn" :class="{'active': display === 'Trade'}" @click="display = 'Trade'">Trade Stock</button> 
                </div>            
                <GamePlayersList v-show="display === 'Leaderboard'" :update="triggerUpdate"/>
                <div class="portfolio-container" v-show="display === 'Portfolio'">
                    <PlayerPortfolio v-if="!isJoinVisible && !isCreator && $store.state.token != ''" :update="triggerUpdate" @openBuyMenu="openFromPortfolio"/>
                    <p v-else>You need to join the game to have a portfolio.</p>
                </div>
                
                <div class="trade" v-show="display === 'Trade'">
                    <p v-if="game.winner != null">This game has ended.</p>
                    <StockDetail :stock="currentStock" :gameId="game_id" @update="update" v-else-if="!isJoinVisible && !isCreator && $store.state.token != '' && Object.keys(currentStock).length > 0"/>
                    <p v-else-if="!isJoinVisible && !isCreator && $store.state.token != ''">Please select a stock to trade.</p>
                    <p v-else>You need to join the game to be able to trade.</p>
                </div>
                        
            </div>      
        </div>
    </div>
</template>

<script>

import GamePlayersList from '../components/GamePlayersList.vue';
import gameService from '../services/GameService';
import stockService from '../services/StockService';
import Modal from "../components/Modal.vue";
import PlayerPortfolio from '../components/PlayerPortfolio.vue';
import StockList from '../components/StockList.vue';
import StockDetail from '../components/StockDetail.vue';

export default {
  components: {
        GamePlayersList, 
        Modal,
        PlayerPortfolio,
        StockList,
        StockDetail
    },
    data() {
        return {
            game_id: "",
            game: {
                game_name: "",
                start_date: "",
                end_date: "",
                creator_id: -1,
                winner: null
            },
            currentStock: {},
            loggedInUsername: this.$store.state.user.username,
            isModalVisible: false,
            isJoinVisible: true,
            isCreator: false,
            triggerUpdate: false,
            display: "Leaderboard"
        };
    },
    methods: {
        endGame() {
            gameService.setWinner(this.game_id).then(response => {
                if (response.status === 200 || response.status === 201) {
                    this.update();
                    this.game.winner = response.data;
                }
            })
        },
        update() {
            this.triggerUpdate = true;
            setTimeout(() => {
                this.triggerUpdate = false;
            }, 500)
        },
        openBuyMenu(stock) {
            this.display = "Trade";
            this.currentStock = stock;      
        },
        showModal(){
            this.isModalVisible = true;
        },
        closeModal(){
            this.isModalVisible = false;
        },
        fetchGame() {
            gameService.getGameById(this.game_id).then((response) => {
                if (response.status === 200 || response.status === 201) {
                    this.game.game_name = response.data.game_name;
                    this.game.start_date = response.data.start_date;
                    this.game.end_date = response.data.end_date; 
                    this.game.creator_id = response.data.creator_id;
                    this.game.winner = response.data.winner;
                    if (this.game.creator_id == this.$store.state.user.id) {
                        this.isCreator = true;
                    }
                }
            })
            .catch((error) => {
                (error);
            });      
        },
        joinGame() {
            gameService.joinGame(this.game_id).then((response) => {
                if (response.status === 200) {
                    this.isModalVisible = false;
                    this.isJoinVisible = false;
                    this.update();
                }
            })
            .catch((error) => {
                (error);
            });
        },      
        openFromPortfolio(stock) {           
            stockService.getStockByTicker(stock.stock_ticker).then((response) => {
                if (response.status === 200 || response.status === 201) {
                    const stock = response.data;
                    stock.change = (stock.c - stock.o) * 100 / stock.o;
                    this.openBuyMenu(stock);
                }
            })  
        }
    },
    created() {
        this.game_id = parseInt(this.$route.params.game_id);
        this.fetchGame();
        gameService.getLeaderboard(this.game_id).then(response => {
            this.players = response.data;
            if (this.players.some(player => player.username === this.loggedInUsername) || this.$store.state.token === '') {
                this.isJoinVisible = false;
            }
        });
    },
}
</script>

<style scoped>
.game {
    margin: 0 auto;
}
.title-background {
    background: radial-gradient(at top left, #4a6384, #778DA9);
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 60px;
    padding: 20px 0px;
}
.info {
    display: flex;
    flex-direction: column;
    gap: 15px;
}
.info > h1, p {
    margin: 0;
}
.title-background > .btn {
  color: white;
  background-color: rgb(27, 38, 59);
  padding: 5px 10px;
  border-radius: 5px;
}
.join-box {
  padding-top: 50px;
  top: 0;
  position: fixed;
  background-color: rgb(0, 0, 0, 0.5);
  width: 100vw;
  height: 100vh;
}
.interface {
    width: 1400px;
    margin: 0px auto;
    height: calc(100vh - 225px);
    display: flex;
    border-left: 1px solid rgb(173, 173, 173);
    border-right: 1px solid rgb(173, 173, 173);
}
.pf-lb {
    width: calc(33% - 5px);
    border-left: 5px solid rgb(114, 114, 114);
    display: flex;
    flex-direction: column;
}
.pf-lb > .buttons {
    display: flex;
    height: 31.5px;
}
.pf-lb > .buttons > .btn {
    border: none;
    width: 34%;
    border-bottom: 1px solid rgb(173, 173, 173);
    cursor: pointer;
}
.pf-lb > .buttons > .btn:nth-child(2) {
    border-right: 1px solid rgb(173, 173, 173);
    border-left: 1px solid rgb(173, 173, 173);
}
.pf-lb > .buttons > .btn.active {
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.3);
  background-color: rgb(227, 226, 226);
}
.portfolio-container {
    background: rgb(151, 167, 194);
    height: calc(100% - 31.5px);
}
.portfolio-container > p {
    padding: 20px;
}
.trade {
    background-color: #1B263B;
    height: calc(100% - 31.5px);  
}
.trade > p {
    color: white;
    padding: 20px;
}
</style>