<template>
    <div class="global-leaderboard">
        <div class="upper">
            <h1>Players Ranking</h1>
        </div>
        <div class="table-container">
            <table>
                <tr>
                    <th>Rank</th>
                    <th>Username</th>
                    <th>Wins</th>
                    <th>Games</th>
                    <th>Win Percentage</th>
                </tr>
                <tr v-for="player in leaderboard" :key="player.username">
                    <td>{{ player.rank }}</td>
                    <td>{{ player.username }}</td>
                    <td>{{ player.win_count }}</td>
                    <td>{{ player.total_games }}</td>
                    <td>{{ player.win_percentage.toFixed(2) }}%</td>
                </tr>
            </table>
        </div>
    </div>
</template>

<script>
import gameService from '../services/GameService';
export default {
    data() {
        return {
            leaderboard: []
        }
    },
    created() {
        gameService.getGlobalLeaderboard().then(response => {
            if (response.status === 200 || response.status === 201) {
                this.leaderboard = response.data
            }
        })
    }
}
</script>

<style scoped>
.global-leaderboard {
    height: calc(100vh - 47.5px);
}
.upper {
  background: radial-gradient(at top left, #4a6384, #778DA9);
  text-align: center;
  padding: 40px 0px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.table-container {
    width: 900px;
    margin: 0px auto;
    background: rgb(240, 240, 240);
    border-left: 1px solid rgb(173, 173, 173);
    border-right: 1px solid rgb(173, 173, 173);
    box-shadow: 4px 0px 8px 3px rgb(95, 95, 95);
    padding: 0 10px;
    overflow-y: auto;
    height: calc(100vh - 207.5px);
}
table {
    border-collapse: collapse;
    table-layout: fixed;
    width: 100%;
}
table > tr {
    border-top: 1px solid rgb(173, 173, 173);
    text-align: center;
}
table > tr:first-child {
    border: none;
}
table > tr > th, table > tr > td {
    padding: 10px 0px;
}
table > tr > th:nth-child(2) {
    width: 30%;
}
</style>