import axios from 'axios';

export default {
    getTradesForUser(gameId) {
        return axios.get(`/games/${gameId}/trades`)
    },
    buyStock(gameId, trade) {
        return axios.post(`/games/${gameId}/buy`, trade)
    },
    sellStock(gameId, trade, all) {
        return axios.post(`/games/${gameId}/sell/${all}`, trade)
    }
}