<template>
    <div class="card" :style="{backgroundImage: `url(${background})`}">
        <h1>{{ game.game_name }}</h1>
        <p class="date">{{new Date(game.start_date).toLocaleDateString()}} - {{new Date(game.end_date).toLocaleDateString()}}</p>
        <p>Creator: {{creator}}</p>
        <h3>Winner: {{game.winner != null ? game.winner : "N/A"}}</h3>
    </div>
</template>
    
    
<script>
    import background1 from '../images/background1.jpg';
    import background2 from '../images/background2.jpg';
    import UserService from '../services/UserService.js';
    export default {
        props: {
            game: Object
        },
        data() {
            return {
                creator: "",
                background: "",
                backgrounds: [background1, background2]
            }
        },
        created() {
            UserService.getUserById(this.game.creator_id).then(response => {
                this.creator = response.data.username
            })
            this.background = this.backgrounds[Math.round(Math.random())];
        },
        computed: {
            getBackgroundImage() {
                return {
                    'background-image': "require('@/images/background.jpg')",
                }
            }
        }
    };
</script>
    
<style scoped>
.card {
    /* background-image: url('../images/background.jpg'); */
    background-size: cover;
    cursor: pointer;
    color: white;
    padding: 20px;
    border-radius: 5px;
}
.card > .date {
    font-size: 14px;
    font-style: italic;
    color: rgb(184, 181, 181);
}
.card > h3 {
    color: gold;
}
</style>
