<template>
  <div class="players">
    <table>
      <tr>
        <th>Rank</th>
        <th>Name</th>
        <th>Total Value</th>
      </tr>
      <tr v-for="player in players" :key="player.username">
        <td v-if="player.rank <= 3">
          <img :src="require('@/images/' + medals[player.rank - 1])" class="medal"/>        
        </td>
        <td v-else>{{ player.rank }}</td>
        <td>{{ player.username }}</td>
        <td>{{ Math.abs(player.game_balance).toFixed(2) }}</td>
      </tr>
    </table>
  </div>
</template>

<script>

import GameService from '../services/GameService';

export default {
  props: {
    update: Boolean,
  },
  data() {
    return {
      players: [],
      medals: ['gold.svg', 'silver.svg', 'bronze.svg']
    }
  },
  created() {
    this.game_id = parseInt(this.$route.params.game_id);
    this.getLeaderboard();
  },
  methods: {
      getLeaderboard() {
        GameService.getLeaderboard(this.game_id).then(response => {
          this.players = response.data; 
        });
      }
  },
  watch: {
    update(newValue) {
      if (newValue) {
        this.getLeaderboard();
      }
    }
  }
}
</script>

<style scoped>
.players {
  background: rgb(151, 167, 194);
  height: calc(100% - 31.5px);
  overflow-y: auto;
}
table {
  border-collapse: collapse;
  width: 100%;
}
table > tr > th {
  padding: 12px 0px;
}

table > tr > td {
  text-align: center;
  padding: 5px 0;
  height: 34px;
}
table > tr:nth-child(odd) {
  background-color: #778DA9;
}
table > tr:nth-child(even) {
  background-color: #8d9eb5;
}
.medal {
  height: 30px;
}
</style>
