import axios from 'axios';

export default {
    getAllStocks() {
        return axios.get('/stocks')
    },
    getStockByTicker(ticker) {
        return axios.get(`/stocks/${ticker}`)
    },
    getStockByDate(ticker, date) {
        return axios.get(`/stocks/${ticker}/${date}`)
    },
    resetDatabase() {
        return axios.post('/stocks/reset/')
    }
}