<template>
  <div class="portfolio">
    <div class="attributes">
      <div class="balance-container">
        <img src="@/images/balance.svg" />
        <p>${{ Math.abs(wallet.balance).toFixed(2) }}</p>
      </div>
      <p>Total Stock Value: ${{ this.totalValue.toFixed(2) }}</p>
    </div>
    <div class="stock-wallet" v-if="stocks.length > 0">
      <table>
        <tr>
          <th>Ticker</th>
          <th>Buy Price</th>
          <th>Price</th>
          <th>Quantity</th>
          <th>Value</th>
          <th>Change</th>
        </tr>
        <tr v-for="stock in stocks" :key="stock.stock_ticker" @click="openBuyMenu(stock)">
          <td>{{ stock.stock_ticker }}</td>
          <td>{{ stock.trade_price.toFixed(2) }}</td>
          <td>{{ stock.current_price.toFixed(2) }}</td>
          <td>{{ stock.stock_quantity.toFixed(2) }}</td>
          <td>
            {{ (stock.current_price * stock.stock_quantity).toFixed(2) }}
          </td>
          <td :class="{increase: stock.change >= 0, decrease: stock.change < 0}">
            {{ stock.change >= 0 ? "+" : ""}}{{ stock.change.toFixed(2) }}%
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import GameService from "../services/GameService";
import PortfolioService from "../services/PortfolioService";

export default {
  props: {
    update: Boolean,
  },
  data() {
    return {
      stocks: [],
      totalValue: 0,
      loggedInUsername: this.$store.state.user.username,
      gameId: parseInt(this.$route.params.game_id),
      wallet: {
        balance: 0,
      },
    };
  },
  created() {
    this.showStocks();
    this.portfolioFill();
  },
  methods: {
    showStocks() {
      PortfolioService.viewCurrentStocks(this.gameId).then((response) => {
        this.stocks = response.data;
        this.totalValue = 0;
        for (const stock of this.stocks) {
          this.totalValue += stock.current_price * stock.stock_quantity;
          stock.change = (stock.current_price - stock.trade_price) * 100 / stock.trade_price
        }
      });
    },
    portfolioFill() {
      GameService.getUserBalance(this.gameId).then((response) => {
        this.wallet.balance = parseFloat(response.data);
      });
    },
    openBuyMenu(stock) {
      this.$emit('openBuyMenu', stock)
    }
  },
  watch: {
    update(newValue) {
      if (newValue) {
        this.portfolioFill();
        this.showStocks();
      }
    },
  },
};
</script>

<style scoped>
.portfolio {
  display: flex;
  flex-direction: column;
}
.portfolio > .attributes {
  display: flex;
  flex-direction: column;
  gap: 5px;
  padding: 15px;
  padding-bottom: 20px;
}
.portfolio > .attributes > .balance-container {
  display: flex;
  align-items: center;
  gap: 10px;
}
p {
  margin: 0;
}
img {
  height: 30px;
}
.portfolio > .stock-wallet {
  overflow-y: auto;
  font-size: 15px;

}
table {
  border-collapse: collapse;
  width: 100%;
}
table > tr > th,
table > tr > td {
  text-align: center;
  padding: 10px 0;
}
table > tr:nth-child(odd) {
  background-color: #778da9;
}
table > tr:nth-child(even) {
  background-color: #8d9eb5;
}
table > tr:has(td) {
    cursor: pointer
}
table > tr:has(td):hover {
  background-color: rgb(90, 101, 130);
}
.decrease {
    color: red;
}
.increase {
    color: rgb(32, 113, 32);
}
</style>